	module.exports = {"data":{"post":{"id":"/Users/zwhitten/Projects/zw_blog/content/posts/2017-08-13--site-theme-switch/index.md absPath of file >>> MarkdownRemark","html":"<p><strong>Just a quick note to anyone who stumbles across the git history of this site.</strong></p>\n<p>When I first started this blog I used the <a href=\"https://github.com/pages-themes/slate\">slate</a> theme since it was suppored by github pages. Out of the box this is great since it gave a clean interface with basically zero work; however, making even simple changes required a lot more work than I thought they should. </p>\n<p>The current iteration of Jekyll obscures a lot of what’s happening with the themes and overriding styles required basically pulling down the git repo of the theme, including the various layouts and styles, and then making the tweaks I desired. </p>\n<p>I was not a fan of this, so I refactored most of the styling to use <a href=\"https://getbootstrap.com/\">bootstrap</a> instead. The look of the site is virtually unchanged, but my piece of mind is much improved. </p>","htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"strong","properties":{},"children":[{"type":"text","value":"Just a quick note to anyone who stumbles across the git history of this site."}]}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"When I first started this blog I used the "},{"type":"element","tagName":"a","properties":{"href":"https://github.com/pages-themes/slate"},"children":[{"type":"text","value":"slate"}]},{"type":"text","value":" theme since it was suppored by github pages. Out of the box this is great since it gave a clean interface with basically zero work; however, making even simple changes required a lot more work than I thought they should. "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The current iteration of Jekyll obscures a lot of what’s happening with the themes and overriding styles required basically pulling down the git repo of the theme, including the various layouts and styles, and then making the tweaks I desired. "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"I was not a fan of this, so I refactored most of the styling to use "},{"type":"element","tagName":"a","properties":{"href":"https://getbootstrap.com/"},"children":[{"type":"text","value":"bootstrap"}]},{"type":"text","value":" instead. The look of the site is virtually unchanged, but my piece of mind is much improved. "}]}],"data":{"quirksMode":false}},"fields":{"slug":"/site-theme-switch/","prefix":"2017-08-13"},"frontmatter":{"title":"Site Theme Switch","subTitle":null,"comments":true,"cover":{"childImageSharp":{"resize":{"src":"/static/markus-gempeler-972977-unsplash-8a416f23efe73cd91ffb5ade2ced48f7-ada8c.jpg"}}}}},"author":{"id":"/Users/zwhitten/Projects/zw_blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Z. Whitten</strong> if you like this post you should check out <a href=\"/\">more</a></p>"},"footnote":{"id":"/Users/zwhitten/Projects/zw_blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this site is the personal blog of <a href=\"https://zdub.io\">@zwhitten</a></li>\n<li>built using <a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby</a></li>\n<li>hosted on <a href=\"https://github.com/zwhitten/zwhitten.github.io\">Github</a></li>\n<li>photos by <a href=\"https://unsplash.com\">unsplash.com</a></li>\n</ul>"},"site":{"siteMetadata":{"title":"Codes of Electric Sheep"}}},"pathContext":{"slug":"/site-theme-switch/"}}

